    <div v-else-if="lang===`en`">
      <h4 class="fw-bold">The night of deportation</h4>

      <p class="fw-bold">The winter at the turn of 1939 and 1940 turned out to be extremely harsh. Snow and frost paralyzed life almost all over Europe. It was a pause for the fighting troops. However, in occupied Poland, winter did not bring any respite. The Gestapo and the NKVD were doing their criminal duty. Especially the Soviets were getting ready for a wide-ranging action in the Polish Eastern Borderlands.
      </p>
      <p> After the "liberation of Western Belarus and Western Ukraine", as Soviet propaganda called the Red Army's aggression against the Polish Borderlands, the political unification of these lands with the Soviet empire was immediately undertaken. The pace was impressive indeed: on October 22, 1939, "elections" to the people's assemblies in Western Belarus and Western Ukraine were held. The "people's representatives" selected during this election farce immediately asked for "Western Belarus and Western Ukraine to be included in the all-union family of Soviet republics." As a result, the Supreme Soviet of the USSR, during its meeting on November 1 and 2, 1939, agreed to include these lands within the borders of the Soviet state. It was then that Vyacheslav Molotov uttered the famous sentence that a short attack by the German and Soviet armies on Poland was enough to leave nothing “of this obscure creation of the Treaty of Versailles”.</p>

      <h4 class="fw-bold">

Great terror
</h4>

      <p>
        The decision to join Kresy to the Soviet Union was followed by another directive, which was to make the lives of their inhabitants even more a nightmare. On November 29, the Supreme Council of the USSR granted Soviet citizenship to all persons who lived in the annexed areas on November 1 and 2. On December 30, 1939, a decree issued them with passports, i.e. Soviet identity cards. This compulsory "passporting" served the NKVD as an excellent way to collect or supplement data on the population of the Borderlands, especially Polish, which was the biggest problem for the Soviet security service at the time. Those assigned Soviet citizenship were immediately conscripted into the Red Army. Thus, 150,000 soldiers were immediately drafted into its working battalions. Poles! Moreover, the NKVD increased the wave of arrests in December 1939. At that time, mainly in borderland towns, many former employees of the Polish administration, judiciary, social and economic activists and reserve officers were imprisoned. These people were deported to labor camps under open judgments or secret tribunals. And that was just the beginning - the start-up of the great terror machine that the NKVD had created to subjugate the Borderlands.
      </p>
      <p>
        The NKVD were well aware that no propaganda or indoctrination would change Poles into "Soviet people." Anyway, they had clear evidence of it. On January 21, 1940, in Czortków (then in the Ukrainian Socialist Soviet Republic), a group of Polish high school students, about 120-150 people, sparked an uprising. Partially armed youth captured the railway station in order to take control of the communication route to Romania in the future, from where it was possible to get to the Polish army in France. After the first panic of the soldiers of the Soviet garrison, the rebellion was quickly suppressed. Eight rebels were executed in Odessa and the rest imprisoned, but this showed that Molotov prematurely announced the definitive end of Poland. The NKVD then resorted to proven methods of repressing not only individual people, but entire communities and social groups. Railway trains were gathered on a mass scale and deported routes were marked out to the farthest regions of the vast Soviet empire.
      </p>

      <h4 class="fw-bold">

A frosty nightmare
</h4>

      <p>
        Along with another wave of frosts and snowstorms, the NKVD began its deportation action on the night of February 9-10, 1940. This terrible night in thousands of Polish towns, settlements and hamlets had the same course, planned in detail by the NKVD. According to the proscription lists, the house of the family designated for deportation was surrounded. Brutally awakened people were allowed to take their personal belongings, kitchen utensils and food for several weeks. Then the displaced persons were taken by sleigh or rushed on foot to the nearest railway station, where cattle wagons were waiting for them on a siding.
      </p>
      <p>
        This is how a resident of Lipowiec near Drohobycz, a teenage Adela Żurawska at the time, remembered this moment: “The Soviet soldiers who surrounded our house gave us a moment to pack the most important things and loaded everyone into a sleigh. We were taken to the railway station in Zborów. There was already a very long train of cattle wagons under the steam. About fifty people were squeezed in ours. We headed east four or five days later. […] There was one cry then - people mourned and prayed ”. This February deportation primarily affected military settlers, foresters and other government officials, as well as railroad workers, along with their families, of course. If the fathers of the families were proved that they were officers, entrepreneurs or landowners, after the verdict of the summary courts, they were sent to labor camps. The rest of those deported in February 1940 - mainly women with children and old people - were transported to the Urals and Siberia, where in dramatically difficult conditions they had to "start their lives anew". Many of them were not able to survive this nightmare, they died of hypothermia and starvation during the transport. The action of February 9-10, 1940, was only the first in a series of deportations that the NKVD planned to cleanse the captured lands of the "Polish element" once and for all. In April, families of prisoners of war, convicted and arrested fell victims of mass deportations. Thus, in Kazakhstan there were, among others, the relatives of Polish officers murdered at the same time in Katyn. In June, refugees from central and western Poland were deported.
      </p>
      <p>
        Contrary to what the President of Russia, Vladimir Putin, proclaims today, among the Polish citizens deported at that time there were thousands of Jews who fled the repressions of Stalin's ally Hitler. Many of them did not survive the transport in cattle cars or died at the place of exile - in Arkhangelsk and Krasnoyarsk. Finally, the last wave of deportations, carried out before the outbreak of the German-Soviet war in May and June 1941, affected refugees from the Vilnius region. They were deported to Kazakhstan and Siberia literally until the last days of peace between the Third Reich and the Soviet Union. The Wehrmacht took over many of the trains with deported people after entering the Borderlands on June 22, 1941. It is very difficult to estimate how many Polish citizens were then deported during these four phases of deportation. The most common number is 400-500 thousand. One thing is certain - everyone suffered a ordeal in the "Soviet paradise". Those who were lucky enough to survive, after the "amnesty" in July 1941, they joined the Polish army of General Władysław Anders and awaited their exodus to the West. And those who "did not make it to Anders" returned to Poland in the ranks of General Zygmunt Berling's army. Such was the fate of Adela Żurawska, quoted above.
      </p>

