    <div v-else-if="lang===`it`">
      <h4 class="fw-bold">La notte della deportazione</h4>

      <p class="fw-bold">L'inverno a cavallo tra il 1939 e il 1940 si è rivelato estremamente rigido. La neve e il gelo hanno paralizzato la vita in quasi tutta Europa. Era una pausa per le truppe combattenti. Tuttavia, nella Polonia occupata, l'inverno non ha portato tregua. La Gestapo e l'NKVD stavano facendo il loro dovere criminale. Soprattutto i sovietici si stavano preparando per un'azione ad ampio raggio nelle terre di confine orientali polacche.
      </p>
      <p> Dopo la "liberazione della Bielorussia occidentale e dell'Ucraina occidentale", come la propaganda sovietica chiamava l'aggressione dell'Armata Rossa contro le terre di confine polacche, fu immediatamente intrapresa l'unificazione politica di queste terre con l'impero sovietico. Il ritmo fu davvero impressionante: il 22 ottobre 1939 si tennero "elezioni" alle assemblee popolari nella Bielorussia occidentale e nell'Ucraina occidentale. I "rappresentanti del popolo" selezionati durante questa farsa elettorale hanno subito chiesto che "la Bielorussia occidentale e l'Ucraina occidentale fossero incluse nella famiglia tutta sindacale delle repubbliche sovietiche". Di conseguenza, il Soviet Supremo dell'URSS, durante la sua riunione del 1 ° e 2 novembre 1939, accettò di includere queste terre entro i confini dello stato sovietico. Fu allora che Vyacheslav Molotov pronunciò la famosa frase che bastava un breve attacco degli eserciti tedesco e sovietico alla Polonia per non lasciare nulla “di questa oscura creazione del Trattato di Versailles”.</p>

      <h4 class="fw-bold">

Grande terrore
</h4>

      <p>
        La decisione di unirsi a Kresy nell'Unione Sovietica è stata seguita da un'altra direttiva, che era di rendere la vita dei loro abitanti ancora più un incubo. Il 29 novembre, il Consiglio supremo dell'URSS ha concesso la cittadinanza sovietica a tutte le persone che vivevano nelle aree annesse il 1 e il 2 novembre. Il 30 dicembre 1939, un decreto ha emesso loro passaporti, cioè carte d'identità sovietiche. Questo "passaporto" obbligatorio servì all'NKVD come un modo eccellente per raccogliere o integrare i dati sulla popolazione delle Terre di confine, in particolare quella polacca, che all'epoca era il problema più grande per il servizio di sicurezza sovietico. Quelli a cui era stata assegnata la cittadinanza sovietica furono immediatamente arruolati nell'Armata Rossa. Così, 150.000 soldati furono immediatamente arruolati nei suoi battaglioni di lavoro. Polacchi! Inoltre, l'NKVD aumentò l'ondata di arresti nel dicembre 1939. A quel tempo, principalmente nelle città di confine, furono imprigionati molti ex dipendenti dell'amministrazione, magistratura, attivisti sociali ed economici e ufficiali di riserva polacchi. Queste persone furono deportate nei campi di lavoro con sentenze aperte o tribunali segreti. E quello era solo l'inizio: l'avvio della grande macchina del terrore che l'NKVD aveva creato per soggiogare Borderlands.


      </p>
      <p>
        L'NKVD era ben consapevole che nessuna propaganda o indottrinamento avrebbe trasformato i polacchi in "popolo sovietico". Comunque, ne avevano una chiara prova. Il 21 gennaio 1940, a Czortków (allora nella Repubblica socialista sovietica ucraina), un gruppo di studenti delle scuole superiori polacche, circa 120-150 persone, scatenò una rivolta. Giovani parzialmente armati hanno catturato la stazione ferroviaria per prendere in futuro il controllo della via di comunicazione con la Romania, da dove è stato possibile raggiungere l'esercito polacco in Francia. Dopo il primo panico dei soldati della guarnigione sovietica, la ribellione fu rapidamente repressa. Otto ribelli furono giustiziati a Odessa e il resto imprigionato, ma questo dimostrò che Molotov annunciò prematuramente la fine definitiva della Polonia. Il NKVD ha quindi fatto ricorso a metodi collaudati per reprimere non solo le singole persone, ma intere comunità e gruppi sociali. I treni ferroviari furono raccolti su larga scala e le rotte deportate furono segnate nelle regioni più lontane del vasto impero sovietico.


      </p>

      <h4 class="fw-bold">

Un gelido incubo
</h4>

      <p>
        
Insieme a un'altra ondata di gelate e tempeste di neve, l'NKVD iniziò la sua azione di deportazione nella notte tra il 9 e il 10 febbraio 1940. Questa notte terribile in migliaia di città, insediamenti e villaggi polacchi ha avuto lo stesso corso, pianificato in dettaglio dall'NKVD. Secondo gli elenchi di proscrizione, la casa della famiglia designata per la deportazione era circondata. Alle persone brutalmente risvegliate è stato permesso di portare i propri effetti personali, utensili da cucina e cibo per diverse settimane. Quindi gli sfollati sono stati portati in slitta o portati di corsa a piedi alla stazione ferroviaria più vicina, dove i carri bestiame li aspettavano su un binario di raccordo.


      </p>
      <p>
        Così ricorda questo momento una residente di Lipowiec vicino a Drohobycz, un'adela Żurawska dell'epoca: “I soldati sovietici che circondavano la nostra casa ci diedero un momento per preparare le cose più importanti e caricare tutti su una slitta. Siamo stati portati alla stazione ferroviaria di Zborów. C'era già un lunghissimo treno di carri bestiame sotto il vapore. Una cinquantina di persone sono state schiacciate nella nostra. Ci siamo diretti a est quattro o cinque giorni dopo. […] Allora ci fu un grido: la gente piangeva e pregava ”. Questa deportazione di febbraio ha colpito principalmente coloni militari, silvicoltori e altri funzionari governativi, oltre ai lavoratori delle ferrovie, insieme alle loro famiglie, ovviamente. Se i padri delle famiglie erano provati che erano ufficiali, imprenditori o proprietari terrieri, dopo il verdetto dei tribunali sommari, venivano mandati nei campi di lavoro. Il resto dei deportati nel febbraio 1940 - soprattutto donne con bambini e anziani - furono trasportati negli Urali e in Siberia, dove in condizioni drammaticamente difficili dovettero "ricominciare la loro vita". Molti di loro non sono riusciti a sopravvivere a questo incubo, sono morti di ipotermia e fame durante il trasporto. L'azione del 9-10 febbraio 1940 fu solo la prima di una serie di deportazioni pianificate dall'NKVD per ripulire le terre conquistate dall '"elemento polacco" una volta per tutte. Ad aprile, famiglie di prigionieri di guerra, condannati e arrestati, sono caduti vittime di deportazioni di massa. Così, in Kazakistan c'erano, tra gli altri, i parenti di ufficiali polacchi assassinati contemporaneamente a Katyn. A giugno sono stati deportati rifugiati dalla Polonia centrale e occidentale.
      </p>
      <p>
        Contrariamente a quanto proclama oggi il presidente della Russia, Vladimir Putin, tra i cittadini polacchi deportati in quel momento c'erano migliaia di ebrei fuggiti dalle repressioni dell'alleato di Stalin, Hitler. Molti di loro non sono sopravvissuti al trasporto in vagoni bestiame o sono morti nel luogo di esilio - ad Arkhangelsk e Krasnoyarsk. Infine, l'ultima ondata di deportazioni, effettuate prima dello scoppio della guerra tedesco-sovietica nel maggio e giugno 1941, colpì i profughi della regione di Vilnius. Furono deportati letteralmente in Kazakistan e Siberia fino agli ultimi giorni di pace tra il Terzo Reich e l'Unione Sovietica. La Wehrmacht si impossessò di molti dei treni con persone deportate dopo essere entrata nelle Borderlands il 22 giugno 1941. È molto difficile stimare quanti cittadini polacchi furono poi deportati durante queste quattro fasi di deportazione. Il numero più comune è 400-500 mila. Una cosa è certa: tutti hanno sofferto un calvario nel "paradiso sovietico". Coloro che hanno avuto la fortuna di sopravvivere, dopo l '"amnistia" del luglio 1941, si sono arruolati nell'esercito polacco del generale Władysław Anders e hanno atteso il loro esodo in Occidente. E quelli che "non ce l'hanno fatta ad Anders" sono tornati in Polonia nelle file dell'esercito del generale Zygmunt Berling. Tale era il destino di Adela Żurawska, citata sopra.
      </p>

